.signupbg {
    padding: 10vw;
    display: none;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.60);
    top: 0px;
    width: calc(80vw - 17px);
    position: absolute;
    .signup {
        width: 30vw;
        padding: 3vw;
        background-color: rgb(255, 255, 255);
        display: grid;
        gap: 20px;
        border-radius: 10px;
        .cross {
            width: min-content;
            height: min-content;
            padding: 10px;
            transform: scaleX(130%);
            cursor: pointer;
            justify-self: right;
        }
        .logo {
            width: 200px;
            height: 50px;
            background-image: url(../../ximg/logo3.svg);
            background-repeat: no-repeat;
            background-size: contain;
            justify-self: center;
        }
        .small {
            font-size: x-large;
            text-align: center;
        }
        input[type="text"] {
            padding: 10px 20px;
            outline: 1px solid #B0B0B0;
            border-radius: 20px;
        }
        .chk {
            display: flex;
            gap: 10px;
            justify-self: center;
            color: #1492E6;
        }
        a {
            color: black;
            justify-self: center;
        }
        .joinas {
            font-weight: 700;
            font-size: large;
            text-align: center;
        }
        .btn1 {
            padding: 10px;
            color: #1C2942;
            border: 1px solid #CFCFCF;
            text-align: center;
            border-radius: 20px;
            cursor: not-allowed;
            background-color: rgba(255, 255, 255, 0);
            width: 100%;
        }
        .btn2 {
            padding: 10px;
            background-color: #1C2942;
            color: white;
            text-align: center;
            border-radius: 20px;
            cursor: not-allowed;
            width: 100%;
        }
    }
}

#signup2 {
    display: none;
    .signup {
        .title {
            font-weight: 600;
        }
        .innerbox {
            border: 1px solid #CFCFCF;
            border-radius: 10px;
            padding: 20px;
            display: grid;
            gap: 10px;
        }
        .sub {
            color: #656565;
        }
        .inputs {
            margin-top: 20px;
            display: grid;
            gap: 10px;
            .place {
                display: flex;
                color: #3d3d3d;
                .comp {
                    color: red;
                }
            }
            select {
                border: 1px solid #C3C3C3;
                padding: 10px;
                margin-bottom: 20px;
                color: #1492E6;
                option {
                    padding: 10px;
                }
            }
        }
        .nextbtn {
            padding: 10px 40px;
            width: min-content;
            background-color: black;
            color: white;
            border-radius: 10px;
            margin-bottom: 20px;
            cursor: pointer;
        }
    }
}

