.bd {
    border: 1px solid rgb(198, 198, 198);
    padding: 10px;
    border-radius: 10px;
    display: grid;
    gap: 20px;
    .btn3 {
        padding: 10px 20px;
        width: fit-content;
        background-color: black;
        color: white;
        border-radius: 10px;
        font-size: small;
        cursor: pointer;
    }
    .checkboxes {
        display: grid;
        justify-content: center;
        align-content: center;
        gap: 3vw;
        grid-template-columns: min-content min-content min-content;
        padding: 10px;
        .checkbx_un {
            display: flex;
            gap: 5px;
            padding: 10px;
            content: "dsf";
            transform: scale(130%);
            width: fit-content;
            align-items: center;
            font-size: x-small;
        }
    }
    .title {
        justify-self: center;
        font-size: large;
        font-weight: 500;
    }
    .innerbox {
        display: grid;
        justify-content: center;
        .input1 {
            display: grid;
            justify-content: center;
        }
        .input2 {
            display: grid;
            justify-content: center;
        }
        img {
            justify-self: center;
        }
    }
    .nextbtn {
        padding: 10px 20px;
        background-color: black;
        color: white;
        width: fit-content;
        border-radius: 10px;
        cursor: pointer;
    }
}

.toggal {
    display: grid;
    gap: 5px;
    p {
        font-size: smaller;
        color: #757575;
    }
    .checkbx {
        padding: 100px;
        content: "dsf";
        transform: scale(130%);
        width: fit-content;
    }
    .checkbx:checked {
        transform: translateX(20px) scale(130%);
    }
    select {
        padding: 10px 20px;
        border: 1px solid #1492E6;
        border-radius: 10px;
        width: 100%;
        color: #1492E6;
    }
}

