#root_1 {
    display: grid;
    .profile_main {
        display: grid;
        gap: 10px;
        grid-template-columns: auto 30vw;
        width: 80vw;
        justify-self: center;
        margin: 40px 0px;
        .profile_left {
            display: grid;
            gap: 10px;
        }
        .profile_right {
            display: grid;
            grid-template-columns: auto;
            align-content: flex-start;
            gap: 10px;
        }
    }
    .section {
        background-color: white;
        height: fit-content;
        padding: 40px;
        border: 1px solid rgb(155, 155, 155);
        display: grid;
        gap: 20px;
        border-radius: 10px;
        .google_switch {
            display: flex;
            gap: 1vw;
            justify-content: center;
        }
        .search {
            display: grid;
            input[type=text] {
                padding: 10px;
                background-color: #F6FCFE;
                border: 1px solid rgb(158, 158, 158);
            }
        }
        p {
            text-align: center;
        }
        button {
            padding: 10px 20px;
            width: 80%;
            border: 1px solid black;
            background-color: white;
            color: black;
            justify-self: center;
            cursor: pointer;
            border-radius: 50px;
        }
        .button_cont {
            display: flex;
            gap: 10px;
            width: fit-content;
        }
        .line {
            margin: 10px 0px;
            height: 1px;
            background-color: #D9D9D9;
        }
        .skills_section {
            background-color: #F6F6F6;
            padding: 20px;
        }
        .profile_image {
            border-radius: 50%;
            outline: 2px solid white;
            box-shadow: 0px 5px 3px rgb(123, 123, 123);
            width: 100px;
            height: 100px;
            transform: translateY(-50%) translateX(10px);
        }
        .cover_image {
            width: 100%;
            object-fit: cover;
            height: 200px;
        }
    }
}

.loading {
    justify-self: center;
    align-self: center;
    width: 100px;
    height: 100px;
    display: none;
}